
import { defineComponent, reactive, toRefs, ref, watch } from "vue";
import { wraprequest } from "@/utils/request";
import { message } from "ant-design-vue";
import { useStore } from "vuex";

export default defineComponent({
  components: {},

  data() {
    return {};
  },
  props: {
    foo: {
      type: String,
      required: false,
    },
  },

  methods: {
    getUserRepositories(): string {
      return "";
    }, // 1
    fnFlushAccount() {
      wraprequest({
        umimethod: "GET",
        umipath: "/my/userinfo",
      })
        .then((res: any) => {
          let { account } = res.data;
          this.store.commit("update_user", res.data);

          return null;
        })
        .catch((err) => {
          message.error(err);
        })
        .finally(() => {});
    },
    fnCommit() {
      if (this.commitLoading) {
        return;
      }
      this.commitLoading = true;
      wraprequest({
        umimethod: "POST",
        umipath: "/home/commit",
        url: this.valuearea,
      })
        .then((res: any) => {
          console.log(res);
          let { msg } = res;
          message.success(msg);
          this.fnFlushAccount();
          return null;
        })
        .catch((err) => {
          message.error(err);
          console.log(err);
        })
        .finally(() => {
          this.commitLoading = false;
        });
    },
  },
  mounted() {
    if (this.user.account == -1) {
      this.fnFlushAccount();
    }
  },
  setup() {
    const store = useStore();
    // let selectedKeys: Array<String> = [];
    const state = reactive({
      pageinfo: "home",
      valuearea: "https://douyin.com/lkdslfj",
      commitLoading: false,
      user: store.state.user,
    });

    return {
      ...toRefs(state),
      store,
    };
  },
});
