
import { defineComponent, reactive, toRefs, ref, watch } from "vue";
import { wraprequest } from "@/utils/request";
import { message } from "ant-design-vue";
import { useStore } from "vuex";
declare var Date: {
  parseRFC3339(item: any): any;
  toRFC3339UTCString(item: any): any;
  toRFC3339LocaleString(item: any): any;
};
export default defineComponent({
  components: {},

  data() {
    return {};
  },
  props: {
    foo: {
      type: String,
      required: false,
    },
  },
  computed: {},

  methods: {
    getUserRepositories(): string {
      return "";
    }, // 1
    TranslateDate(item: string): any {
      let d1 = Date.parseRFC3339(item);
      return d1.toLocaleString("cn", { hour12: false });
      // return d1.toLocaleString("", { hour12: false });
    },
    fnLogin() {
      if (this.commitLoading) {
        return;
      }
      this.commitLoading = true;
      wraprequest({
        umimethod: "POST",
        umipath: "/auth/login",
        username: this.valnumber.toString(),
        password: this.valpassword.toString(),
      })
        .then((res: any) => {
          console.log(res);
          let that = this;
          let { access_token } = res.data;
          if (access_token) {
            localStorage.setItem("access_token", access_token);
            message.success("登录成功");
            this.fnFlushAccount();
            this.fnHistoryList();
            setTimeout(function () {
              that.showLogin = false;
            }, 100);
          }
          return null;
        })
        .catch((err) => {
          message.error(err);
          console.log(err);
        })
        .finally(() => {
          this.commitLoading = false;
        });
    },

    fnClearCookie() {
      localStorage.setItem("access_token", "");
      window.location.reload();
    },
    fnFlushAccount() {
      wraprequest({
        umimethod: "GET",
        umipath: "/my/userinfo",
      })
        .then((res: any) => {
          let { account } = res.data;
          this.store.commit("update_user", res.data);

          return null;
        })
        .catch((err) => {
          message.error(err);
        })
        .finally(() => {});
    },
    fnFirshPage() {
      this.loadposition = 0;
      this.finishall = false;
      this.fnFlushButton();
    },
    fnFlushButton() {
      if (this.finishall) {
        return;
      }
      if (this.flagLoading) {
        return;
      }
      this.flagLoading = true;
      this.fnFlushAccount();
      this.fnHistoryList();
    },
    fnHistoryList() {
      wraprequest({
        umimethod: "GET",
        umipath: "/my/consume",
        page: 1 + this.loadposition,
        page_size: this.page_size,
      })
        .then((res: any) => {
          let { consumerlist } = res.data;
          if (consumerlist) {
            this.consumerlist = consumerlist;
            this.loadposition++;
            if (consumerlist.length != this.page_size) {
              this.finishall = true;
            }
          }
          console.log(res);
          return null;
        })
        .catch((err) => {
          message.error(err);
        })
        .finally(() => {
          this.flagLoading = false;
        });
    },
  },
  mounted() {
    console.log("mounted");
    let token = localStorage.getItem("access_token");
    if (!token) {
      this.showLogin = true;
    } else {
      if (this.user.account == -1) {
        this.fnFlushAccount();
      }
      this.fnHistoryList();
    }
  },
  setup() {
    const store = useStore();

    const state = reactive({
      pageinfo: "my",
      showLogin: false,
      valnumber: "372292",
      valpassword: "123",
      consumerlist: [],
      commitLoading: false,
      flagLoading: false,
      user: store.state.user,
      page_size: 10,
      loadposition: 0,
      finishall: false,
    });

    return {
      ...toRefs(state),
      store,
    };
  },
});
