
import { defineComponent, reactive, toRefs, ref, watch } from "vue";
import { wraprequest } from "@/utils/request";
import { message } from "ant-design-vue";
import ListMy from "@/components/ListMy.vue";
export default defineComponent({
  components: {},

  data() {
    return {};
  },
  props: {
    foo: {
      type: String,
      required: false,
    },
  },

  methods: {
    checkCopy(status: number) {
      if (status == 3) {
        return true;
      }
    },
    fnCopyUrl(value: string) {
      var inputTest = document.createElement("input");
      inputTest.value = value;
      document.body.appendChild(inputTest);
      inputTest.select();
      document.execCommand("Copy");
      inputTest.className = "oInput";
      inputTest.style.display = "none";
      message.success("成功复制到链接");
    },
    getUserRepositories(): string {
      return "";
    }, // 1
    fnGetStatus(status: number) {
      // 状态 1: 未处理  2:正在处理 3:已完成 4:失败
      if (status == 1) {
        return "未处理";
      }
      if (status == 2) {
        return "正在渲染";
      }
      if (status == 3) {
        return "已完成";
      }
      if (status == 4) {
        return "失败";
      }
    },
  },
  mounted() {
    wraprequest({
      umimethod: "GET",
      umipath: "/detail/today",
    })
      .then((res: any) => {
        let { consumerlist } = res.data;
        if (consumerlist) {
          this.listToday = consumerlist;
        }
      })
      .catch((err) => {
        message.error(err);
      })
      .finally(() => {});

    wraprequest({
      umimethod: "GET",
      umipath: "/detail/history",
    })
      .then((res: any) => {
        let { consumerlist } = res.data;
        if (consumerlist) {
          this.listHistory = consumerlist;
        }
        return null;
      })
      .catch((err) => {
        message.error(err);
      })
      .finally(() => {});
  },
  setup() {
    const state = reactive({
      pageinfo: "detail",
      activeKey: "1",
      listToday: [],
      listHistory: [],
    });

    return {
      ...toRefs(state),
    };
  },
});
