
import { defineComponent, reactive, toRefs, ref, watch } from "vue";
// import { allmenu } from "@/store/";
import store from "@/store/";
import { mapState } from "vuex";
import { useRoute, useRouter } from "vue-router";
export default defineComponent({
  components: {},

  data() {
    return {};
  },
  props: {},

  methods: {
    fnMenuClass(id: string) {
      if (id == this.currentPath) {
        return "focus";
      }
    },
    changePage(page: string) {
      this.parchangePage(page);
    },
    fnHeight() {
      var pageWidth = window.innerWidth;
      var pageHeight = window.innerHeight;
      return {
        height: pageHeight,
      };
    },
  },
  // computed: mapState([
  //   "allmenu", // 映射 this.count 为 store.state.count
  // ]),
  setup() {
    let openKeys: Array<string> = [];
    const router = useRouter();
    const route = useRoute();
    let selectedKeys: Array<String> = [];
    const state = reactive({
      currentPath: "home",
      showinfo: "",
    });

    const handleClick = (e: Event) => {
      console.log("click", e);
    };
    const titleClick = (e: Event) => {
      console.log("titleClick", e);
    };

    let fnSetOpen = () => {
      if (route && route.path) {
        if (route.path == "/") {
          state.currentPath = "home";
        }

        if (route.path == "/my") {
          state.currentPath = "my";
        }

        if (route.path == "/detail") {
          state.currentPath = "detail";
        }
        state.showinfo = route.path;
        // if(route.path )
      }
      state.showinfo = "11w";
      console.log(route);
    };
    fnSetOpen();

    watch(
      () => route.path,
      (val, oval) => {
        fnSetOpen();
      }
    );

    let parchangePage = (val: string) => {
      if (val == "home") {
        router.push("/");
      } else {
        router.push(val);
      }
    };
    return {
      ...toRefs(state),
      handleClick,
      titleClick,
      parchangePage,
    };
  },
});
